.signup {
  font-size: 30px;
  margin-top: 20px;
  margin-bottom: 40px;
}
.login {
  color: rgb(0, 60, 255);
  width: max-content;
}
.login:hover {
  text-decoration: underline;
  cursor: pointer;
}
.errorMsg {
  color: red;
}
.smallCard {
  display: none;
}
@media not all and (min-resolution:.001dpcm) { 
  @supports (-webkit-appearance: none) {
    .signup{
      font-size: 20px;
      margin-top: 10px;
      margin-bottom: 0px;
    }
    .login  {
      color: rgb(0, 60, 255);
      font-size: 15px;
    }
  }
}
/* ------------------------ */
@media only screen and (max-width: 768px) {
  .bigCard {
    display: none;
  }
  .smallCard {
    display: block;
  }
  .signup {
    font-size: 20px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .login {
    font-size: 13px;
    margin-bottom: 0px;
  }
  .errorMsg{
    font-size: 13px;
  }
}
